.numbertext {
  color: $colorMain;
  font-size: 20px;
  font-weight: bold;
  padding: 8px 12px;
  position: absolute;
  top: 0;
}


.dot {
  cursor:pointer;
  height: 25px;
  width: 25px;
  margin: 0 2px;
  background-color: $colorMain;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
}

.active, .dot:hover {
  background-color: rgba(255, 0, 0, 0.8);
}
